.home {

    .slideshow-container {
        .content-wrap {
            max-width: 1300px;
            margin: 0 auto;
        }

        .content {
            display: flex;
            flex-direction: column;
            justify-content: start;
            align-items: flex-start;
            top: 10%;
            position: absolute;
            max-width: 650px;
            padding: 1em;

            h1,
            p,
            a {
                color: white;
            }

        }
    }

    .site-content {
        padding: 0;
    }

    #slide-first-element {
        display: block;
        width: 100%;
        height: auto;
    }

    #slide-element-2,
    #slide-element-3 {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }

    .slide-image {
        width: 100%;
        border-radius: 20px;
    }

    .slideshow-container {
        position: relative;
        margin: auto;
    }

    #slide-first-element {
        animation: fade-1 13s infinite;
        -webkit-animation: fade-1 13s infinite;
    }

    #slide-element-2 {
        animation: fade-2 13s infinite;
        -webkit-animation: fade-2 13s infinite;
    }

    #slide-element-3 {
        animation: fade-3 13s infinite;
        -webkit-animation: fade-3 13s infinite;
    }

    @keyframes fade-1 {
        0% {
            opacity: 1;
        }

        33% {
            opacity: 0;
        }

        66% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }
    }

    @keyframes fade-2 {
        0% {
            opacity: 0;
        }

        33% {
            opacity: 1;
        }

        66% {
            opacity: 0;
        }

        100% {
            opacity: 0;
        }
    }

    @keyframes fade-3 {
        0% {
            opacity: 0;
        }

        33% {
            opacity: 0;
        }

        66% {
            opacity: 1;
        }

        100% {
            opacity: 0;
        }
    }




    .homepage-programs-wrap {
        max-width: 1300px;
        margin: 50px auto;
        padding: 0 1em;

        .homepage-programs {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;

            .image-wrap {
                height: 270px;
                width: auto;
                background-repeat: no-repeat;
                border: 10px solid $blue;

                .image {
                    background-size: cover;
                    min-height: 1px;
                    height: 100%;
                    width: 100%;
                    transition: all .4s;

                    &:hover {
                        cursor: pointer;
                        transform: scale(1.11);
                    }
                }

            }

            .program-1 {
                .image-wrap {
                    .image {
                        background-image: url(https://placebear.com/270/270);
                    }
                }
            }

            .program-2 {
                .image-wrap {
                    .image {
                        background-image: url(https://www.placecage.com/270/270);
                    }
                }
            }

            .program-3 {
                .image-wrap {
                    .image {
                        background-image: url(https://picsum.photos/270/270);
                    }
                }
            }

            .program-4 {
                .image-wrap {
                    .image {
                        background-image: url(https://baconmockup.com/270/270);
                    }
                }
            }

            .program-wrap {
                width: 97%;
                display: grid;
                grid-template-rows: 270px 1fr 2fr 1fr;
            }

            .program-heading {
                font-size: 24px;
                font-weight: 700;
                margin: 15px 0;
            }

            .program-wrap {
                p:not(.program-heading) {
                    font-size: 16px;
                    line-height: 26px;
                }

                .read-more {
                    // display: block;
                    margin-top: 10px;
                }

                .button.primary {
                    margin-top: 10px;
                }

                // .read-more.button {}
            }

        }
    }


}

.home-testimonials-wrap {
    min-height: 420px;
    background-color: $blue;
    margin: 50px 0;



    .home-testimonials {
        max-width: 1300px;
        min-height: 420px;
        margin: 0 auto;
        background: $blue url('/wp-content/themes/ccc/images/kid-red.png');
        background-repeat: no-repeat;
        background-size: auto 90%;
        background-position: 98% center;
        padding: 2em 0;
        color: white;
        text-align: center;

        // .slider {
        //     margin-bottom: 2em;
        // }

        h2 {
            // margin-top: 0;
        }

        .bx-wrapper {
            box-shadow: 0 0 0 #ccc;
            border: 0px solid #fff;
            background: transparent;

        }

        .bx-wrapper {
            max-width: 65% !important;
            margin: 0 auto;

            .bx-viewport {
                padding-bottom: 1em;
            }

            .bx-pager {
                padding-bottom: 20px;
            }
        }



    }
}